.team {
  width: 100%;
  background: linear-gradient(
    258.18deg,
    #e3f3ff 0.86%,
    rgba(227, 243, 255, 0) 98.72%
  );
  .container {
    padding-top: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    p {
      text-align: center;
    }
    .allTeam {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-flow: row wrap;
      padding-bottom: 80px;
      .teamBox {
        width: 260px;
        padding: 70px 0 30px;
        margin-right: 8px;
        img {
          width: 260px;
          height: 210px;
        }
        h1 {
          font-size: 24px;
          line-height: 36px;
          margin: 15px 0;
          margin-bottom: 1px;
        }
        span {
          font-style: normal;
          font-weight: normal;
          font-size: 20px;
          line-height: 30px;
          text-transform: uppercase;
          color: #708ca0;
        }
        p {
          text-align: start;
          padding-top: 15px;
          font-size: 18px;
          line-height: 27px;
        }
      }
      @media (max-width: 650px) {
        flex-flow: column;
        padding: 30px 0 70px;
        .teamBox {
          width: 100%;
          padding: 30px 0;
          margin-right: 0;
          img {
            width: 100%;
            height: auto;
          }
          h1 {
            margin-top: 10px;
          }
          p {
            padding-top: 15px;
          }
        }
      }
    }
  }
}
