.bannerChild {
  width: 100%;
  background-color: #f3f5f7;
  .container {
    padding: 120px 0;
    padding-bottom: 90px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      width: 40%;
    }
    .bannerBox {
      width: 54%;
      span {
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 30px;
        color: #074269;
      }
      h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 50px;
        color: #074269;
        margin-bottom: 10px;
      }
      p {
        font-size: 20px;
        line-height: 35px;
      }
      .bottom {
        margin-top: 35px;
        a{
            width: max-content;
        }
      }
    }
    @media (max-width: 760px) {
      padding: 40px 0;
      flex-flow: column;
      align-items: flex-start;
      img {
        width: 75%;
        order: 1;
        margin-bottom: 20px;
      }
      .bannerBox {
        width: 100%;
        order: 2;
        h1 {
          font-size: 30px;
          line-height: 39px;
        }
        p {
          font-size: 18px;
          line-height: 27px;
        }
        .bottom {
          a.btn {
            width: 100%;
            justify-content: center;
            padding: 8px 0;
          }
        }
      }
    }
  }
}
